* {
  /* font-family: Lato; */
}

audio {
  display: none;
}

.dice-container {
  height: 100px;
}

.dice {
  border: 1px solid black;
  border-radius: 10px;
  font-size: 45px;
  width: 75px;
  height: 75px;
  margin-left: auto;
  margin-right: auto;
  
}


#gameContainer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding-bottom: 40px; 
}

#hand-container .btn {
  margin-right: 25px;
}

#hand {
  font-size: 100px;
  cursor: pointer;
}

button {
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}


